import axios, { Axios, CreateAxiosDefaults } from 'axios'
import { BNify } from '../../core'
import { IntegrationClientModel } from '../integration-client.model'

export class InstadappClient implements IntegrationClientModel {
  // Axios instance
  private axios: Axios

  constructor() {
    this.axios = this.initAxios()
  }

  /**
   * Axios initialization
   */
  private initAxios() {
    const options: CreateAxiosDefaults = {
      baseURL: 'https://api.instadapp.io/v2/mainnet/lite/users/',
      headers: {
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  // Methods

  public async getApr(tokenSymbol: string): Promise<number> {
    switch (tokenSymbol) {
      case 'stETH':
        return this.getStETHApr()
      default:
        return 0
    }
  }

  public async getStETHApr(): Promise<number> {
    return this.axios
      .request({
        url: '0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE/vaults',
        method: 'GET',
        responseType: 'json',
      })
      .then((response) => {
        if (!response?.data) {
          return 0
        }

        const foundVault = response.data.find(
          (r: any) => r.vault === '0xA0D3707c569ff8C87FA923d3823eC5D81c98Be78'
        )
        return BNify(foundVault?.apy.apyWithoutFee).toNumber()
      })
      .catch(() => 0)
  }
}
